.filter-line{display:grid;min-height:24px;max-width:550px;min-width:min-content;padding:5px 10px;box-sizing:content-box;align-items:flex-start;grid-gap:2px;grid-template-columns:70px 110px 100px 120px 24px}.filter-line__remove{height:100%;display:flex;align-items:center;justify-content:center}.filter-line__remove .button-dm{height:24px;width:24px;padding:0;flex:none}.filter-line__column{min-height:24px}.filter-line__column.conjunction{display:flex;align-items:center;justify-content:flex-end;width:75px}.filter-line__column.operation{flex:0}.filter-line__column.value{flex:1;display:grid;grid-auto-flow:column}.filter-line__group{flex:0;display:grid;grid-template-columns:min-content 1fr;grid-gap:2px;align-items:flex-start}.filter-line__selector{flex:1;display:flex;align-items:center;justify-content:space-between}