.filters{background-color:var(--color-neutral-background);position:relative}.filters:not(.filters_sidebar){padding-top:var(--spacing-tight);min-width:400px;border-radius:var(--corner-radius-small);box-shadow:0 3px 6px -4px rgba(0,0,0,.12),0 6px 16px 0 rgba(0,0,0,.08),0 9px 28px 8px var(--black_5)}.filters_sidebar{width:100%}.filters_sidebar .filter-line{padding-right:var(--spacing-tight);padding-left:var(--spacing-tight);align-items:stretch}.filters_sidebar .filter-line .ant-divider{margin:0;height:24px}.filters_sidebar .filter-line__field{width:100%}.filters_sidebar .filter-line__settings{flex-direction:column}.filters_sidebar .filter-line__group{flex:1;padding:2px 0;width:100%}.filters__actions{display:flex;margin-top:var(--spacing-base);padding:0 var(--spacing-tight) var(--spacing-tight);justify-content:space-between}.filters__actions .button-dm{border:1px solid var(--color-neutral-border)}.filters__actions .button-dm:hover{border:1px solid var(--color-neutral-border)}.filters__empty{padding:0 var(--spacing-tight);font-size:14px;color:var(--color-neutral-content-subtler)}.filters__list{padding:var(--spacing-tight) var(--spacing-tight) var(--spacing-tighter)}.filters__list_withFilters{display:grid;grid-template-columns:75px min-content min-content 1fr min-content;grid-gap:4px}.filters_sidebar .filters__list{padding:var(--spacing-base) var(--spacing-tight) var(--spacing-tighter)}.filters_sidebar .filters__list_withFilters{grid-template-columns:1fr 1fr 24px;grid-row-gap:4px;grid-auto-flow:row;align-items:center}.filter-data-tag{margin:1px;font-size:12px;padding:2px 4px;display:inline-flex;align-items:center}